@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #f9fbfd;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1366px;
  margin: 0 auto;
  padding-left: 103px;
  padding-right: 103px;
}
@media (max-width: 1439px) {
  .container {
    padding-left: 103px;
    padding-right: 103px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 15px 47px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.logo svg {
  color: #f9fbfd;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .logo svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo svg {
    width: 42px;
    height: 22px;
  }
}

.header {
  position: absolute;
  z-index: 1;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding-top: 34px;
  padding-bottom: 34px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

@media (max-width: 767px) {
  .header__navigation {
    display: none;
    width: 100%;
  }
}

.header__navigation-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 33px;
}
@media (max-width: 1023px) {
  .header__navigation-list {
    column-gap: 24px;
  }
}
@media (max-width: 767px) {
  .header__navigation-list {
    flex-direction: column;
    column-gap: 0;
    row-gap: 10px;
    padding: 0 30px 0 15px;
  }
}

.header__navigation-link {
  display: block;
  cursor: pointer;
  font-weight: 500;
}
@media (max-width: 1023px) {
  .header__navigation-link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header__navigation-link {
    position: relative;
    display: block;
    padding: 10px 0 8px 10px;
    color: #011c40;
    font-size: 18px;
    line-height: 22px;
  }
}

.header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    margin-top: -9px;
    margin-right: -8px;
    padding: 0;
    background-color: transparent;
    border: none;
    box-sizing: border-box;
    cursor: pointer;
  }
}

.header__toggle-line {
  position: relative;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  transition: 0.2s all;
  transform-origin: left center;
}
.header__toggle-line::before, .header__toggle-line::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  transform-origin: center;
  transition: 0.2s all;
}
.header__toggle-line::before {
  top: 6px;
}
.header__toggle-line::after {
  top: -6px;
}

@media (max-width: 767px) {
  .is-open.header {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    background-color: transparent;
  }
  .is-open .header__container {
    display: grid;
    grid-template-rows: auto 1fr;
    row-gap: 24px;
    background-color: #f9fbfd;
    clip-path: inset(0 0 0 36px);
    padding-left: 51px;
  }
  .is-open .header__logo svg {
    color: #011c40;
  }
  .is-open .header__toggle {
    margin-right: -15px;
  }
  .is-open .header__toggle-line {
    background-color: transparent;
  }
  .is-open .header__toggle-line::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .is-open .header__toggle-line::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .is-open .header__toggle-line::before, .is-open .header__toggle-line::after {
    top: 50%;
    left: 50%;
    width: 18px;
    background-color: #011c40;
  }
  .is-open .header__navigation {
    grid-column: 1/-1;
  }
  .is-open .header__navigation-list {
    padding-left: 0;
  }
}

.hero__container {
  position: relative;
  width: 100%;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
@media (max-width: 1023px) {
  .hero__container {
    filter: none;
  }
}

.hero__image {
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
  background-color: #9fc5e8;
}
.hero__image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.hero__content {
  padding: 270px 0 278px 95px;
}
@media (max-width: 1023px) {
  .hero__content {
    padding: 267px 0 276px;
  }
}
@media (max-width: 767px) {
  .hero__content {
    padding: 175px 0 178px;
  }
}
.hero__content h2,
.hero__content p {
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.hero__content h2 {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .hero__content h2 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .hero__content h2 {
    font-size: 36px;
    line-height: 125%;
  }
}
.hero__content p {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .hero__content p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .hero__content p {
    font-size: 16px;
    line-height: 140%;
  }
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
.custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
.custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
.custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  display: flex;
  width: 500px;
  padding: 14px 0 10px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    padding: 14px 0 10px 0;
    width: 100%;
  }
}
.custom-toggle:last-of-type {
  padding: 14px 0 58px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle:last-of-type {
    padding: 14px 0 58px 0;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 52px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1.5px solid #2c39f2;
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background-color: rgba(44, 57, 242, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #2c39f2;
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  border: 1.5px solid #2c39f2;
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  background-color: #2c39f2;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
.custom-toggle .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}
.custom-toggle .input-message.is-valid {
  color: #15ab07;
}

